* {
    margin: 0;
    padding: 0;
}

html {
    box-sizing: border-box;
  }
  *, *:before, *:after {
    box-sizing: inherit;
  }

html {
    margin: 0px;
    min-height: 100vh;
    min-width: 100vw;
}
 
body {
    margin: 0px;
    min-height: 100vh;
    min-width: 100vw;
    overflow-x: hidden;
}